body, h2, p {
  margin: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0 10px 50px;
  display: block;
}

.nav {
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link:hover, .nav__link:focus, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.product-cards {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.product-cards:hover {
  scale: 1.2;
}

.product-cards__image {
  width: 160px;
  height: 134px;
  margin-left: 3px;
  display: block;
}

.product-cards__info {
  flex-direction: column;
  margin-top: 40px;
  display: flex;
}

.product-cards__info-title {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-cards__info-product-code {
  color: #616070;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-cards__raiting {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.product-cards__raiting-rewiews {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

.product-cards__prise {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.product-cards__prise-title {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-cards__prise-value {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-cards__button-link {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.product-cards__button-link:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.product-cards:hover .product-cards__info-title {
  color: #34568b;
}

.stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 14px;
  width: 16px;
  height: 16px;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}
